<template>
  <!-- <v-layout row> -->
  <v-flex xs12 sm6 lg3 pa-3>
    <v-card height="100%" flat>
      <v-card-title class="headline">
        <v-icon medium class="pr-2">{{info.icon}}</v-icon>
        {{info.header}}
      </v-card-title>
      <v-list one-line>
        <template v-for="(item) in info.subitems">

          <v-list-tile :key="item.name" avatar @click="testAlert" class="tile" :class="item.completed ? 'completed' : 'incomplete'">
            <v-list-tile-avatar>
              <v-avatar :color="item.completed ? 'green lighten-4' : 'grey lighten-4'" class="icon_class"><v-icon>{{item.icon}}</v-icon></v-avatar>
            </v-list-tile-avatar>
            <v-list-tile-content>
              <v-list-tile-title class="item_text">
                {{item.name}}
              </v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>

          <div :key="item.name + '1'" v-if="item.subitems" >
          <v-list-tile class="tile-subitem"
          v-for="subitem in item.subitems"
          :key="subitem.name" 
          :class="subitem.completed ? 'completed' : 'incomplete'" 
          @click="testAlert">
          <v-list-tile>
            <v-list-tile-avatar>
              <v-avatar :color="subitem.completed ? 'green lighten-4' : 'grey lighten-4'" class="icon_class"><v-icon>{{subitem.icon ? subitem.icon : item.icon}}</v-icon></v-avatar>
            </v-list-tile-avatar>

            <v-list-tile-content>
              <v-list-tile-title>{{subitem.name}}</v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
          </v-list-tile>
          </div>
        </template>
      </v-list>
    </v-card>
  </v-flex>
</template>
<script>
export default {
  props: ["info"],
  methods: {
    testAlert(e) {
      alert("click action");
    }
  }
};
</script>

<style scoped>

.tile{
  margin-top: 5px;
}

.tile-subitem{
  margin-top: 5px;
  margin-left:20px;
}

